html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #eee;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.header__navigation {
  margin: 0;
}

.main {
  justify-content: center;
  gap: 46px 48px;
  margin: 0;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

.navigation__list {
  align-items: flex-end;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.navigation__item {
  text-transform: uppercase;
}

.navigation__item:not(:last-child) {
  margin-right: 20px;
}

.navigation__link {
  color: #060b35;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.navigation__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 37px;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__name {
  letter-spacing: 0;
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  letter-spacing: 0;
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rates {
  justify-content: space-between;
  align-items: center;
  margin: 16px 0 0;
  display: flex;
}

.card__rates-reviews {
  letter-spacing: 0;
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin: 24px 0 0;
  display: flex;
}

.card__price-text {
  letter-spacing: 0;
  color: #616070;
  margin: 0;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-cost {
  letter-spacing: 0;
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  align-items: center;
  display: flex;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}
